.content_1 {
  margin: 5px 0;
  background: #fff;
  padding: 20px 20px 60px;
  border-radius: 0px;
  box-shadow: 0 0 3px #eee;
  min-height: calc(100vh - 341px);
  position: relative;
  box-sizing: border-box;
}
.content_1 .homework-status .ant-select-selection {
  background: #F4F4F4;
  width: 188px;
  height: 28px;
  font-size: 14px;
  border-radius: 0;
}
.content_1 .homework-status .ant-select-selection .ant-select-selection__rendered {
  line-height: 28px;
}
.content_1 .content-list {
  border: 1px solid #F3F3F3;
  margin-top: 20px;
  padding: 15px 30px 15px 20px;
  border-radius: 3px;
}
.content_1 .content-list .operation div {
  position: absolute;
  top: 32px;
  right: 5px;
  background: #fff;
  z-index: 1000;
  width: 90px;
  height: 70px;
  background: #FFFFFF;
  box-shadow: 0px 16px 25px #EBEBEB;
  border-radius: 5px;
  display: none;
}
.content_1 .content-list .operation div p:hover {
  color: #3E78ED;
}
.content_1 .content-list .operation div p {
  margin: 0;
  line-height: 34px;
  text-align: center;
  cursor: pointer;
}
.content_1 .content-list .operation div p:nth-of-type(1) {
  border-bottom: 1px solid #eee;
  position: relative;
  z-index: 1;
  background: #fff;
}
.content_1 .content-list .operation div::after {
  content: "";
  width: 16px;
  height: 16px;
  transform: rotate(45deg);
  background: #fff;
  position: absolute;
  top: -8px;
  box-shadow: 0px 16px 25px #CCCCCC;
  right: 20px;
}
.content_1 .content-list img {
  width: 27px;
  height: 27px;
  margin-right: 12px;
}
.content_1 .content-list .more {
  position: absolute;
  top: 10px;
  right: 28px;
  cursor: pointer;
  z-index: 999;
}
.content_1 .content-list .operation:hover div {
  display: block;
}
.content_1 h2 {
  font-size: 16px;
  font-family: Alibaba PuHuiTi;
  font-weight: bold;
  color: #333333;
  width: 600px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  vertical-align: middle;
}
.content_1 .content-top .title {
  display: flex;
  margin-bottom: 12px;
}
.content_1 .content-top span {
  margin-right: 20px;
}
.content_1 .content-top .ant-col-6 {
  display: flex;
  text-align: center;
  font-size: 16px;
  justify-content: flex-end;
}
.content_1 .content-top .ant-col-6 dl {
  cursor: pointer;
  margin: 0;
  margin-top: 15px;
  margin-left: 40px;
}
.content_1 .content-top .ant-col-6 dl:last-child {
  color: #3E78ED;
}
.content_1 .content-bottom {
  border-top: 1px solid #ccc;
  background: linear-gradient(to right, #e2e2e2, #fff);
  padding: 10px;
  line-height: 30px;
}
.content_1 .content-bottom .ant-btn {
  float: right;
  margin-left: 10px;
}
.content_1 .content-bottom span {
  margin: 0 10px;
}
.empty-l {
  height: auto !important;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
